@charset "UTF-8";
@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700,900);

body{
  padding: 100px;
  font-family: 'Roboto',sans-serif;
  font-weight: 300;
  height:100vh;


  background:
  	repeating-radial-gradient(
  		circle,
  		transparent,
  		transparent 3.5em,
  		rgba(255, 99, 71, 0.1) 3.5em,
  		rgba(255, 99, 71, 0.1) 4.5em
  	),
  	repeating-radial-gradient(
  		circle,
  		transparent,
  		transparent 3.5em,
  		rgba(141, 199, 255, 0.1) 3.5em,
  		rgba(141, 199, 255, 0.1) 4.5em
  	),
  	repeating-radial-gradient(
  		circle,
  		transparent,
  		transparent 2.5em,
  		rgba(240, 224, 137, 0.1) 2.5em,
  		rgba(240, 224, 137, 0.1) 2.75em
  	), floralwhite;

  background-blend-mode: multiply;
  background-size: 10em 10em;
  background-position:
  	0 0,
  	5em 5em,
  	10em 5em;
}
h1.greet-message{
  text-align: center;
  font-weight: 300;
}
ul.list-group{
  list-style: none;
  margin:20px auto;
  padding:0;
  text-align: center;
  li{
    display: inline-block;
    padding: 10px;
    background: #FFEB3B;
    margin: 0 5px;
    font-size:30px;
    /*transform: skewX(7deg) skewY(2deg);*/
  }
}
.fork-me{
  position: absolute;
  top: 0;
  right: 0;
  border: 0;
  max-width: 50%;
  cursor:pointer;
  img{
    max-width: 100%;
    cursor:pointer;
  }

}
.white-opaque{
    width: 90%;
    background: white;
    height: 90%;
    max-width: 1100px;
    max-height: 500px;
    margin: auto;
    box-shadow: 0px 0px 43px -17px;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}
.playground{
    height: 100%;
    opacity: 0;
    max-height:0px;
    transition: all .5s;
    &.shown{opacity:1;max-height:1000px;}
}
.playground div.col-xs-6:first-child{
  background: rgba(240, 240, 240, 0.4);
}
.playground div.col-xs-6{
  padding:50px;height: 100%;
}
ul.list-group.active_vectors_bar{
  position: fixed;
  bottom: 0;
  width: 100%;
  transform: translateX(-50%);
  left: 50%;
  margin: 0;
  background: #1e8df0;
  padding: 10px 10px 0;
  border-radius: 10px 10px 0 0;
  display:none;
}
.card_wrap{
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
}
.card_border{padding:30px;}
.card_border{
    transform-origin: center;
  padding: 20px;
  border: 10px solid #ffcc00;
  background: #ffffff;
  border-radius: 50%;
  position: relative;
  /*transform: translate(-50%,-50%);
  left: 50%;
  top: 50%;*/
  margin: 50px auto;
  transform: rotate(0deg);
  width: 300px;
  height: 300px;
}
.card_border>div{
  position: relative;
  display:block;
  width: 100%;
  height: 100%;
}
.card_border>div{    padding: 20px;
    box-sizing: border-box;
    border-radius: 50%;}
.card_border a{transform: translate(-50%,-50%);}
.card_border a {
    position: absolute;
    font-size: 40px;
}
.card_border a:hover{
    text-decoration: none;}

.game_header{
  position: absolute;
  top: 0;
  left: 0;
  max-width: 90%;
  width: 300px;
  padding: 20px;
  box-sizing: border-box;
  z-index: 1;
  background: white;
  border-radius: 0 0 30px 0;
  box-shadow: 0px 0px 63px -15px;
  line-height:60px;
  font-weight: 100;
}
.game_header h2{margin:0;font-weight: 100;line-height:40px;font-size:26px;}
.main-screen{
    position: relative;
    height: 100%;
    max-height:1000px;
    opacity:1;
    transition: all .5s;
    width: 100%;
    margin: auto;
    & > .col-xs-12 {height:100%;}
    &.hidden{opacity:0;max-height:0px;}
    .content{
        padding:20px;
        box-sizing: border-box;
        min-width:500px;
        min-height:200px;
        position:absolute;
        top:50%;
        left:50%;
        transform: translate(-50%,-50%);
        text-align:center;
        font-size:30px;
    }
}

@-webkit-keyframes flash-red {
    0% {
        background-color: transparent;
        opacity:1;
    }
    22% {
        background-color: rgba(255, 199, 203, 0.5);
    }
    50% {
        background-color: rgba(255, 232, 159, 0.5);
    }
    75% {
        background-color: rgba(255, 199, 203, 0.5);
    }
    100% {
        background-color: transparent;
    }
}
@-webkit-keyframes rotating {
    0% {
        transform: rotate(0deg);
    }
    22% {
        transform: rotate(-180deg);
    }
    50% {
        transform: rotate(-360deg);
    }
    75% {
        transform: rotate(-540deg);
    }
    100% {
        transform: rotate(-7200deg);
    }
}

.flash-red {
    -webkit-animation-name: flash-red;
    -webkit-animation-duration: 100ms;
    -webkit-animation-iteration-count: 1;
    -webkit-animation-timing-function: ease-in-out;
}

.rotating{
    -webkit-animation-name: rotating;
    -webkit-animation-duration: 150ms;
    -webkit-animation-iteration-count: 1;
    -webkit-animation-timing-function: ease-in-out;
    transform-origin: center;
}
.score_box{
    position:absolute;
    width:100%;
    text-align:center;
    font-size:80px;
    line-height: 200px;
    color:#352929;
    text-shadow:2px 2px 0px #f0f0f0;
    top:0;
    left:0;
    z-index: 10;
}
